import React from 'react'
import Home from './Home'
import Users from './Users'
import Posts from './Posts'
import { BrowserRouter, Route, Link } from 'react-router-dom'
import UserShow from './UsersShow'
import PostShow from './PostsShow'



function App(props) {
  return (
    <BrowserRouter>
      <div className="navbar navbar-expand-lg navbar-light bg-light">
        <h1><span class='text-primary'>BLOG UI</span></h1>&nbsp;
        <Link className="navbar-brand" href="#" to='/'>Home</Link>&nbsp;
        <Link className="navbar-brand" href="#" to='/Users'>Users</Link>&nbsp;
        <Link className="navbar-brand" href="#" to='/Posts'>Posts</Link>&nbsp;
      </div>

      <Route path='/' component={Home} exact={true} />
      <Route path='/Users' component={Users} exact={true} />
      <Route path='/Posts' component={Posts} exact={true} />
      <Route path='/users/:id' component={UserShow} />
      <Route path='/posts/:id' component={PostShow} />


    </BrowserRouter>
  )
}

export default App
